  box-sizing: border-box;
}
 
body {
  font-family: 'Poppins', sans-serif;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}
/*
  AppBar
*/
 
.app-bar {
  background-color: white;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}
 
.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}
 
.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}
 
.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}
 
.app-bar .app-bar__brand h1 {
  font-family: 'Lobster', cursive;
  font-size: 18px;
}
 
.app-bar .app-bar__navigation {
  position: absolute;
  top: 40px;
  left: -230px;
  width: 200px;
  transition: all 0.3s;
  padding: 8px;
  background-color: white;
  overflow: hidden;
}
 
.app-bar .app-bar__navigation.open {
  left: 0;
}

.app-bar .app-bar__navigation ul li{
  list-style: none;
}
 
.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: black;
  padding: 12px;
  margin-bottom: 5px;
  width: 100%;
}

button#hamburgerButton{
	width: 44px;
	height: 44px;
}

.hero{
	width:100%;
	height:500px;
	display:flex;
	align-items:center;
	justify-content:center;
	position:relative;
	flex-direction:column
}

.hero-img-container{
	width:100%;
	height:100%;
	position:absolute
}

.hero-img{
	width:100%;
	height:100%;
	object-fit:cover;
	margin:0;
	padding:0
}

.hero-content{
	color:white;
	padding:0;
	margin:0;
	z-index:100;
	max-width:500px;
	text-align:center
}
 
/*
  Main Content
*/
 
main {
	padding: 4rem;
	display: flex;
	gap: 1.5rem;
}

#content {
	padding: 16px;
	display: flex;
	flex-flow: column nowrap;
	gap: 1rem 2rem;
	margin-bottom: 32px;
	align-items: center;
}

.content {
	padding: 0px 24px 0px 24px;
}

.content h1{
	text-align: center;
}

.group-card{
  margin: 32px auto auto;
	text-align: left;
	display: grid;
	grid-row-gap: 16px
}

.card {
	border: 1px solid rgb(209, 209, 209);
	margin-bottom: 10px;
	border-radius: 5px;
	width: 100%;
	overflow: hidden;
	position: relative;
}

.top-img{
	height: 200px;
	width: auto;
}

.top-img img{
	height: 200px;
	width: 100%;
	object-fit: cover;
	object-position: center center;
	position: absolute;
}

.card-content{
  	padding: 16px
}

.card-city {
	position: absolute;
	top: 18px;
	left: 24px;
	border-radius: 5px;
	background-color: #198754;
	padding: 5px 15px 5px 15px;
	color: white;
	font-weight: 500
}

.rating {
	font-size: 12px;
	color: white;
	display: inline-block;
	background-color: #FFC107;
	padding: 4px 8px 4px 8px;
	margin-bottom: 8px;
	border-radius: 5px;
}

.card-title{
	margin: 0;
	font-weight: bold;
	font-size: 24px;
	text-decoration: none;
	color: black;
	padding: 12px 0px;
}

.card-text{
	font-size: 12px;
	line-height: 1.5em
}

.black-font{
  	color: black !important;
}

.card-img:hover{
	box-shadow: 0 2px 8px 2px rgba(0, 0, 0, 0.2);
	border: none;
}

.img-content{
  width: 100%;
  max-height: 500px;
}

.pm-0{
  margin: 0;
  padding: 0;
}

.location-container{
  display: flex; 
  align-items: center;
}

.categories-container{
  display: flex;
  flex-wrap: wrap;
}

.categories {
	font-size: 12px;
	color: white;
	display: inline-block;
	background-color: #198754;
	padding: 4px 8px 4px 8px;
	margin-bottom: 8px;
	border-radius: 5px;
  margin-right: 8px;
}

.mb{
  margin-bottom: 8px;
}

.menus-container{
  display: flex; 
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: start;
}

.menu-table{
  width:48%; 
  margin-bottom: 16px; 
  border-collapse: collapse; 
  text-align: center;
}

.review-header{
  display: flex; 
  justify-content: space-between; 
  align-items: center;
  padding: 0;
  margin: 0
}
 
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
  z-index: 5;
}

.skip-link {
	position: absolute;
	top: -40px;
	right: 0;
	padding: 12px;
	background-color: transparent;
	color: white;
	z-index: 100;
}

.skip-link:focus {
  	top: 0;
		color: black;
}
 
#content:focus{
	outline: none;
}
 
/*
  Footer
*/
 
footer {
	text-align: center;
	padding: 20px;
	color: #fff;
	background-color: #454545;
	width: 100%;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAGA;EACE,sBAAsB;AACxB;;AAEA;EACE,kCAAkC;EAClC,uBAAuB;EACvB,iBAAiB;EACjB,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,UAAU;AACZ;AACA;;CAEC;;AAED;EACE,uBAAuB;EACvB,aAAa;EACb,oCAAoC;EACpC,SAAS;EACT,gBAAgB;EAChB,MAAM;EACN,WAAW;EACX,0CAA0C;AAC5C;;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,6BAA6B;EAC7B,YAAY;EACZ,eAAe;EACf,YAAY;EACZ,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,+BAA+B;EAC/B,eAAe;AACjB;;AAEA;EACE,kBAAkB;EAClB,SAAS;EACT,YAAY;EACZ,YAAY;EACZ,oBAAoB;EACpB,YAAY;EACZ,uBAAuB;EACvB,gBAAgB;AAClB;;AAEA;EACE,OAAO;AACT;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,qBAAqB;EACrB,qBAAqB;EACrB,YAAY;EACZ,aAAa;EACb,kBAAkB;EAClB,WAAW;AACb;;AAEA;CACC,WAAW;CACX,YAAY;AACb;;AAEA;CACC,UAAU;CACV,YAAY;CACZ,YAAY;CACZ,kBAAkB;CAClB,sBAAsB;CACtB,iBAAiB;CACjB;AACD;;AAEA;CACC,UAAU;CACV,WAAW;CACX;AACD;;AAEA;CACC,UAAU;CACV,WAAW;CACX,gBAAgB;CAChB,QAAQ;CACR;AACD;;AAEA;CACC,WAAW;CACX,SAAS;CACT,QAAQ;CACR,WAAW;CACX,eAAe;CACf;AACD;;AAEA;;CAEC;;AAED;CACC,aAAa;CACb,aAAa;CACb,WAAW;AACZ;;AAEA;CACC,aAAa;CACb,aAAa;CACb,wBAAwB;CACxB,cAAc;CACd,mBAAmB;CACnB,mBAAmB;AACpB;;AAEA;CACC,0BAA0B;AAC3B;;AAEA;CACC,kBAAkB;AACnB;;AAEA;EACE,sBAAsB;CACvB,gBAAgB;CAChB,aAAa;CACb;AACD;;AAEA;CACC,oCAAoC;CACpC,mBAAmB;CACnB,kBAAkB;CAClB,WAAW;CACX,gBAAgB;CAChB,kBAAkB;AACnB;;AAEA;CACC,aAAa;CACb,WAAW;AACZ;;AAEA;CACC,aAAa;CACb,WAAW;CACX,iBAAiB;CACjB,8BAA8B;CAC9B,kBAAkB;AACnB;;AAEA;GACG;AACH;;AAEA;CACC,kBAAkB;CAClB,SAAS;CACT,UAAU;CACV,kBAAkB;CAClB,yBAAyB;CACzB,0BAA0B;CAC1B,YAAY;CACZ;AACD;;AAEA;CACC,eAAe;CACf,YAAY;CACZ,qBAAqB;CACrB,yBAAyB;CACzB,wBAAwB;CACxB,kBAAkB;CAClB,kBAAkB;AACnB;;AAEA;CACC,SAAS;CACT,iBAAiB;CACjB,eAAe;CACf,qBAAqB;CACrB,YAAY;CACZ,iBAAiB;AAClB;;AAEA;CACC,eAAe;CACf;AACD;;AAEA;GACG,uBAAuB;AAC1B;;AAEA;CACC,4CAA4C;CAC5C,YAAY;AACb;;AAEA;EACE,WAAW;EACX,iBAAiB;AACnB;;AAEA;EACE,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,eAAe;AACjB;;AAEA;CACC,eAAe;CACf,YAAY;CACZ,qBAAqB;CACrB,yBAAyB;CACzB,wBAAwB;CACxB,kBAAkB;CAClB,